<template>
    <section class="msite">
        <!---->
        <HeaderTop :title="address.name">
            <router-link to="/search" class="header_search" slot="left">
                    <i class="iconfont icon-sousuo"></i>
            </router-link>
            <router-link :to="userInfo._id ? '/userInfo': '/login'" class="header_login" slot="right">
                <span class="header_login_text" v-if="!userInfo._id">|</span>
                <span class="header_login_text iconfont icon-wode" v-else></span>
            </router-link>
        </HeaderTop>
        <!---->
        <nav class="msite_nav">
            <div class="swiper-container" v-if="categorys.length">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="(categorys, index) in categorysArr" :key="index">
                        <a href="javascript:" class="link_to_food" v-for="(category, index) in categorys" :key="index">
                            <div class="food_container">
                                <img :src="`${baseImageUrl}${category.image_url}`">
                            </div>
                            <span>{{category.title}}</span>
                        </a>
                    </div>
                </div>
                <!-- Add Pagination -->
                <div class="swiper-pagination"></div>
            </div>
            <img src="./images/msite_back.svg" v-else>
        </nav>
        <!---->
        <div class="msite_shop_list">
            <div class="shop_header">
                <i class="iconfont icon-xuanxiang"></i>
                <span class="shop_header_title"></span>
            </div>
            <ShopList></ShopList>
        </div>
    </section>
</template>

<script>
    import Swiper from 'swiper'
    import Vue from 'vue'
    import 'swiper/dist/css/swiper.min.css'
    import {mapState} from 'vuex'
    import HeaderTop from '../../components/HeaderTop/HeaderTop.vue'
    import ShopList from '../../components/ShopList/ShopList.vue'

    export default {
        data () {
            return {
                baseImageUrl: 'https://fuss10.elemecdn.com'
            }
        },
        mounted () {
            this.$store.dispatch('getCategorys')
            this.$store.dispatch('getShops')

        },
        methods: {},

        computed: {
            ...mapState(['address', 'categorys', 'userInfo']),
            /**
             * categorys 2
             * 8
             */
            categorysArr () {

                const {categorys} = this

                const arr = []
                let minArr = []
                // categorys

                categorys.forEach(c => {

                    //minArr
                    if (minArr.length === 0) {
                        arr.push(minArr)
                    }
                    minArr.push(c)
                    //minArrminArr
                    if (minArr.length === 8) {
                        minArr = []
                    }
                })
                return arr
            },

        },

        components: {
            HeaderTop,
            ShopList,
        },
        watch: {
            categorys (newVal, oldVal) {
                this.$nextTick(() => {      //
                    //Swiper
                    new Swiper('.swiper-container', {
                        loop: true,   //
                        pagination: {
                            el: '.swiper-pagination'
                        }
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" ref="stylesheet/stylus">
    @import "../../common/stylus/mixins.styl"
    .msite //
        width 100%
        .msite_nav
            bottom-border-1px(#e4e4e4)
            margin-top 45px
            height 200px
            background #fff
            .swiper-container
                width 100%
                height 100%
                .swiper-wrapper
                    width 100%
                    height 100%
                    .swiper-slide
                        display flex
                        justify-content center
                        align-items flex-start
                        flex-wrap wrap
                        .link_to_food
                            width 25%
                            .food_container
                                display block
                                width 100%
                                text-align center
                                padding-bottom 10px
                                font-size 0
                                img
                                    display inline-block
                                    width 50px
                                    height 50px
                            span
                                display block
                                width 100%
                                text-align center
                                font-size 13px
                                color #666
                .swiper-pagination
                    > span.swiper-pagination-bullet-active
                        background #02a774
        .msite_shop_list
            top-border-1px(#e4e4e4)
            margin-top 10px
            background #fff
            .shop_header
                padding 10px 10px 0
                .shop_icon
                    margin-left 5px
                    color #999
                .shop_header_title
                    color #999
                    font-size 14px
                    line-height 20px
</style>



